/* -foreditor */
import './index.less';
import React from 'react';
import Editor from 'for-editor';
import ReactMarkdown from 'react-markdown/with-html';
import { Row, Col, Modal, Empty, Tooltip, Divider, Tag } from 'antd';

class Brafteditor extends React.Component {
  handleChange = (value) => {
    this.setState({
      value
    });
  };
  showfullscreen = (info) => {
    this.setState({ visible: info });
  };
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
      value: ''
    };
  };
  render() {
    const { value } = this.state;
    const toolbar = {
      h1: true, // h1
      h2: true, // h2
      h3: true, // h3
      h4: true, // h4
      img: true, // 
      link: true, // 
      code: true, // 
      preview: false, // 
      expand: true, // 
      undo: true, // 
      redo: true, // 
      save: false, // 
      subfield: false // 
    };
    return (
      <Row className="markdown">
        <Col xs={24} sm={24} md={24} lg={12} xl={12}>
          <Divider>
            <Tag color="blue"></Tag>
          </Divider>
          <Editor value={value}
            toolbar={toolbar}
            onChange={this.handleChange} />
        </Col>
        <Col xs={24} sm={24} md={24} lg={12} xl={12}>
          <Divider>
            <Tooltip placement="topLeft" title="">
              <Tag color="blue"
                className="showfullscreen"
                onClick={() => this.showfullscreen(true)}></Tag>
              </Tooltip>
          </Divider>
          {
            window.$globalmethod.isEmpty(value) ?
            <Empty description="" /> :
            <ReactMarkdown className="markdown-output-content" source={value} />
          }
          <Modal title=""
            footer={null}
            width="100vw"
            style={{top:0}}
            mask={false}
            wrapClassName="outputContent"
            visible={this.state.visible}
            onCancel={() => this.showfullscreen(false)}
            bodyStyle={{overflow: 'auto', height:'calc(100% - 56px)', borderRadius: 0, margin: 0, padding: 0}}>
            <div className="braft-output-content" dangerouslySetInnerHTML={{__html: value}}></div>
          </Modal>
        </Col>
      </Row>
    );
  }
}

export default Brafteditor;
