/*  */
import './index.less';
import React from 'react';
import Brafteditor from './brafteditor/index.jsx';
import Foreditor from './foreditor/index.jsx';
import { Tabs } from 'antd';

const { TabPane } = Tabs;

class Editors extends React.Component {
  render() {
    return (
      <div className="editors">
        <Tabs defaultActiveKey="editor">
          <TabPane tab="" key="editor">
            <Brafteditor />
          </TabPane>
          <TabPane tab="Markdown" key="markdown">
            <Foreditor />
          </TabPane>
        </Tabs>
      </div>
    );
  }
}

export default Editors;
