/* -brafteditor */
import './index.less';
import 'braft-editor/dist/output.css';
import 'braft-editor/dist/index.css';
import 'braft-extensions/dist/table.css';
import 'braft-extensions/dist/color-picker.css';
import 'braft-extensions/dist/emoticon.css';
import 'braft-extensions/dist/code-highlighter.css';
import React from 'react';
import BraftEditor from 'braft-editor';
import { ContentUtils } from 'braft-utils';
import Table from 'braft-extensions/dist/table';
import ColorPicker from 'braft-extensions/dist/color-picker';
import Emoticon, { defaultEmoticons } from 'braft-extensions/dist/emoticon';
import CodeHighlighter from 'braft-extensions/dist/code-highlighter';
import { Row, Col, Modal, Empty, Tooltip, Divider, Tag, Input, message } from 'antd';

const { TextArea } = Input;

class Brafteditor extends React.Component {
  handleEditorChange = (editorState) => {
    this.setState({
      editorState: editorState,
      outputHTML: editorState.toHTML()
    });
  };
  showfullscreen = (info) => {
    this.setState({ visible: info });
  };
  clearContent = () => {
    this.setState({
      editorState: ContentUtils.clear(this.state.editorState)
    })
  };
  insertText = () => {
    this.setState({
      visibleText: true,
      text: 'Hello World!'
    });
  };
  handleTextChange = (e) => {
    this.setState({
      text: e.target.value
    })
  };
  handleOk = () => {
    if (window.$globalmethod.isEmpty(this.state.text)) {
      message.warning('');
    } else {
      this.setState({
        visibleText: false,
        editorState: ContentUtils.insertText(this.state.editorState, this.state.text)
      })
    }
  };
  handleCancel = () => {
    this.setState({
      visibleText: false,
      text: 'Hello World!'
    });
  };
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
      visibleText: false,
      text: '',
      editorState: BraftEditor.createEditorState(null),
      extendControls: [{
        key: 'clear-editor',
        type: 'button',
        text: '',
        onClick: this.clearContent
      }, {
        key: 'insert-text',
        type: 'button',
        text: '',
        onClick: this.insertText
      }],
      options: {
        table: {
          defaultColumns: 3,
          defaultRows: 3,
          withDropdown: false,
          exportAttrString: '',
          includeEditors: ['brafteditorInner']
        },
        color: {
          theme: 'light',
          includeEditors: ['brafteditorInner']
        },
        emoticon: {
          emoticons: defaultEmoticons.map(item => require(`braft-extensions/dist/assets/${item}`)),
          includeEditors: ['brafteditorInner']
        },
        code: {
          includeEditors: ['brafteditorInner']
        }
      }
    };
  };
  render() {
    const { editorState, outputHTML, extendControls, options, text } = this.state;
    BraftEditor.use(Table(options.table));
    BraftEditor.use(ColorPicker(options.color));
    BraftEditor.use(Emoticon(options.emoticon));
    BraftEditor.use(CodeHighlighter(options.code));
    return (
      <Row className="brafteditor">
        <Col xs={24} sm={24} md={24} lg={12} xl={12}>
          <Divider>
            <Tag color="blue"></Tag>
          </Divider>
          <BraftEditor id="brafteditorInner"
            className="brafteditorInner"
            value={editorState}
            onChange={this.handleEditorChange}
            extendControls={extendControls}
            ref={instance => this.editorInstance = instance} />
        </Col>
        <Col xs={24} sm={24} md={24} lg={12} xl={12}>
          <Divider>
            <Tooltip placement="topLeft" title="">
              <Tag color="blue"
                className="showfullscreen"
                onClick={() => this.showfullscreen(true)}></Tag>
              </Tooltip>
          </Divider>
          {
            window.$globalmethod.isEmpty(outputHTML) ?
            <Empty description="" /> :
            <div className="braft-output-content" dangerouslySetInnerHTML={{__html: outputHTML}}></div>
          }
          <Modal title=""
            visible={this.state.visibleText}
            onOk={this.handleOk}
            onCancel={this.handleCancel}>
            <TextArea rows={4}
              value={text}
              onChange={e => this.handleTextChange(e)}
              placeholder="" />
          </Modal>
          <Modal title=""
            footer={null}
            width="100vw"
            style={{top:0}}
            mask={false}
            wrapClassName="outputContent"
            visible={this.state.visible}
            onCancel={() => this.showfullscreen(false)}
            bodyStyle={{overflow: 'auto', height:'calc(100% - 56px)', borderRadius: 0, margin: 0, padding: 0}}>
            <div className="braft-output-content" dangerouslySetInnerHTML={{__html: outputHTML}}></div>
          </Modal>
        </Col>
      </Row>
    );
  }
}

export default Brafteditor;
